import { Fade  } from "react-awesome-reveal";
import { useRef } from "react";
import React from 'react'
import iWrold from '../Style/imges/iworld.png'
import '../Style/project.css'
import Netflex from '../Style/imges/Netflix.png'
import project1 from '../Style/imges/project1.mp4'
import { Player } from 'video-react'
const Projects = () => {

    const vidRef = useRef(null);

    const requestFullScreen = (elem) => {
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) {
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) {
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) {
            elem.msRequestFullscreen();
        }
    }

    const openFullScreen = () => {
        requestFullScreen(vidRef.current)
    }

    return (
        <Fade delay={250} >
        <div id="project" className="project-main">
            <h1>Project</h1>
            <div className="projects">
                <div className="project-1">
                    <video width="450"  controls ref={vidRef} onClick={openFullScreen} >
                        <source src={project1} />
                    </video>
                    <div className="project-inner">
                        <h2>e-commerce</h2>
                        <p> a humble e-commerce stall emerges - a testament to simplicity. No flashy banners, no dizzying menus, just clean lines and curated wares. lines, a world unfolds, a simple e-commerce symphony in the online marketplace.<a href="https://github.com/siddharthan21/E-Comm-react-js" target="_blank">View Code</a></p>
                    </div>
                </div>
                <div className="project-1">
                <img src={iWrold} width={450} alt="" />
                    <div className="project-inner">
                        <h2>e-Service Center</h2>
                        <p> ...crafted each section with care. Sure, there's a tapestry of services, but weaving them into a clear, uncluttered layout was a balancing act. Still, I believe visitors will find what they need, bite-sized and easy to navigate, amidst the iWorld brand story.<a href="https://iworldeservice.in/" target="_blank">Link</a> </p>
                    </div>
                </div>
                <div className="project-1">
                    <img src={Netflex} width={450} alt="" />
                    <div className="project-inner">
                        <h2>Netflix clone</h2>
                        <p>
                            As a web-devops engineer, I proudly crafted a Netflix clone page,  ready to captivate audiences with seamless navigation and binge-worthy experiences<a href="https://netflix-clone.tiiny.site/" target="_blank">Link</a>
                            </p>
                    </div>
                </div>
            </div>
        </div>
        </Fade>
    )
}

export default Projects
